import React from 'react';

import css from './settings.styl';

class Settings extends React.Component{
    constructor (props){
        super (props);

        /*
        * English - en, Russian - ru, Italian - it, Spanish - es (or sp), Ukrainian - uk (or ua),
        * German - de, Portuguese - pt, Romanian - ro, Polish - pl, Finnish - fi, Dutch - nl,
        * French - fr, Bulgarian - bg, Swedish - sv (or se), Chinese Traditional - zh_tw,
        * Chinese Simplified - zh (or zh_cn), Turkish - tr, Croatian - hr, Catalan - ca */
    }

    componentDidMount (){

    }

    render (){
        return (
            <div class={css.settings}>
                <div class={css.field}>
                    <label></label>
                    <select>
                        <option>OpenWeatherMap</option>
                    </select>
                </div>
                <div class={css.field}>
                    <label></label>
                    <input type="text"/>
                </div>
                <div class={css.field}>
                    <label> </label>
                    <input type="text"/>
                </div>
                <div class={css.field}>
                    <label> </label>
                    <input type="text"/>
                </div>
                <div class={css.field}>
                    <label>  ( ,    *))</label>
                    <input type="text"/>
                </div>
            </div>
        )
    }
}

export default Settings;


