import React from 'react';

import GeneralInfo from './general-info.jsx';
import CityInfo from './city-info.jsx';
import DetailInfo from './detail-info.jsx';
import Forecast from './forecast.jsx';
import css from './../style.styl';

class Weather extends React.Component {
    constructor (props){
        super (props);
        
    }

    componentDidMount (){

    }

    render (){
        let classTabContent = css.weather + (this.props.settings.showTab=='weather' ? '' : " " + css.hide_tab);
        return (
            <div className={classTabContent}>
                <CityInfo name={this.props.weather.name} country={this.props.weather.sys.country}
                          id={this.props.weather.id} />
                <GeneralInfo weather={this.props.weather.weather[0]} temperature={this.props.weather.main.temp}
                             settings={this.props.settings}/>
                <Forecast weather={this.props.forecast}/>
                <DetailInfo detail={this.props.weather}/>
            </div>
        )
    }
}

export default Weather;


